import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex)
const store= new vuex.Store({
  state:{
    user_count: 0,
    user_info: {
      name: 'huangenai',
      age: 22,
      sex: '女'
    },
    user_cart: []
  },
  getters:{/* 通过this.$store.getters.getlist 调用 */
    /* getlist:function(state){
      return state.list.filter(item => item.age >= 23)
    },
    getcount:function(state){
      return state.count;
    }, */
	get_cart_list:function(state){
		return state.user_cart;
	}
  },
  mutations:{/* 通过this.$store.commit 调用 */
      /* increment:function(state,count) {
        state.count=count;
      } */
	add_cart:function(state,goods){
		state.user_cart.push(goods);
	},
	add_goods_cart_num:function(state,obj){
		state.user_cart[obj.index].nums++;
	},
	reduce_goods_cart_num:function(state,obj){
		state.user_cart[obj.index].nums--;
	}
   },
   actions:{/* 通过this.$store.dispatch('setCount', {count: 123}) 主要用于提交mutations */
     commit_goods_to_add_cart:function({commit},data){
       commit('add_cart',data);
     },
	 commit_addgoodsnums_to_carlist:function({commit},data){
		 commit('add_goods_cart_num',data);
	 },
	 commit_reducegoodsnums_to_carlist:function({commit},data){
	 		 commit('reduce_goods_cart_num',data);
	 }
   }
})
export default store
